import React, {useEffect} from 'react';
import {useParams} from "react-router-dom";
import {elements} from './DataBase.js';

import {ReactComponent as Tppan3} from './maps/Tppan3.svg';
import {ReactComponent as Tppan4} from './maps/Tppan4.svg';
import {ReactComponent as Tppan5} from './maps/Tppan5.svg';
import {ReactComponent as Kkenhus1} from './maps/Kken1.svg';
import {ReactComponent as Kkenhus4} from './maps/Kken4.svg';
import {ReactComponent as Kkenhus5} from './maps/Kken5.svg';
import {ReactComponent as Back} from './icons/back.svg';
import { Link } from "react-router-dom";

function getRoomByName(room){
    const found = elements.find(element => {
        return element.room == room;
    });
    return found;
}

function LocationDetails(){
    var mapName;
    const {roomName} = useParams();
    const room = getRoomByName(roomName);
    
    if(room){
        mapName = room.building + room.floor;
        console.log(mapName);
    }
    else{
        mapName = roomName;
        console.log(mapName);
    }
    
    useEffect(() => {
        if(room){
            document.querySelector("#"+roomName).style.fill = "#00B3E1";
        }
        }, []);

        function changeFloor(){

        }

    return(
        <div className="details">
            <Link to="/">
                <div id="tillbakaKnapp">
                <Back/>   
                </div>       
            </Link>

            {mapName=='Tppan3' &&
                <Tppan3/>
            }
            {mapName=='Tppan4' &&
                <Tppan4/>
            }
            {mapName=='Tppan5' &&
                <Tppan5/>
            }
            
            {mapName=='Kkenhus4' &&
                <Kkenhus4/>
            }
            {mapName=='Kkenhus5' &&
                <Kkenhus5/>
            }
            
            {mapName=='Tappan' &&
            <>
                <Tppan3/>
                <div id='floorContainer'>
                <button onClick={changeFloor}>Vning 3</button>
                <button onClick={changeFloor}>Vning 4</button>
                <button onClick={changeFloor}>Vning 5</button>
                </div>
            </>
            }
            {mapName=='Kakenhus' &&
            <>
                <Kkenhus1/>
                <div id='floorContainer'>
                <button onClick={changeFloor}>Vning 1</button>
                <button onClick={changeFloor}>Vning 2</button>
                <button onClick={changeFloor}>Vning 3</button>
                <button onClick={changeFloor}>Vning 4</button>
                <button onClick={changeFloor}>Vning 5</button>
                </div>
            </>
            }
            
            {mapName!=roomName &&
                <div id="description">
                    <h1>{room.room}</h1>
                    <h2>Ligger i {room.building}</h2>
                    <h2>Vning {room.floor}</h2>
                </div>
            }
        </div>

    )
}
export default LocationDetails;